import React from 'react';
import ServicesImage from '../../assets/images/ServicesImage/ServicesBg.png';
import ServicesBusinessImage from '../../assets/images/ServicesImage/ServicesBussines.png';
import DevelopmentImage from '../../assets/images/ServicesImage/ServicesDevelopmentImage.png'

const ServicesBusiness = () => {
  return (
      <> 
<div className='services-business'>
    <img className='services-business-bg' src={ServicesImage}/>
    <div className='services-business-container'>
        <div className='services-business-text'>
            <h1>WE MOVE YOUR BUSINESS</h1>
            <h2>To The Future Of Digital World With Our Services</h2>
        </div>
        <div className='services-business-image'>
            <img src={ServicesBusinessImage} alt='business-services'/>
        </div>
    </div>


</div>

<div className='services-devlopment'>
    <img src={DevelopmentImage} alt='development'/>
</div>
</>
  )
  }

export default ServicesBusiness